<template>
  <div class="ExhibitionListView">
    <h2>Exhibitions <small>({{count}})</small></h2>
    <input v-model="search" placeholder="filter">
    <vpaginate 
      v-model="page"
      :count="Math.ceil(resultsCount/limit)">
    </vpaginate>
    <table>
      <thead>
        <td width="50%">title</td>
        <td width="60px">opening</td>
        <td width="60px">closing</td>
        <td width="20%">gallery</td>
      </thead>
      <tbody>
        <tr 
        v-for='e in exhibitions' 
        v-bind:key="e.id"
        v-bind:class="{notExhibition: !e.isExhibition}"
        >
          <td>
            <router-link v-bind:to="{
              name: 'exhibition', 
              params: {id: e.id}}">
              {{e.title}}
            </router-link>
          </td>
          <td>
            <small>{{e.openingDate}}</small>
          </td>
          <td>
            <small>{{e.closingDate}}</small>
          <td>
            <router-link :to="{
              name: 'gallery', 
              params: {id: e.gallery_id}}">
              {{e.gallery}}
            </router-link>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
  import store from '../store'
  import vpaginate from '../components/vpaginate'
  export default {
    name: 'ExhibitionListView',
    components: {
      vpaginate
    },
    store,
    data: function(){
      return {
        search: "",
        limit: 15,
        page: 1
      };
    },
    
    computed:{
      exhibitions: function(){
        if(this.$store.state.database == undefined)
          return []

        // query database
        let sql = `
        SELECT e.ikonid, e.title, e.openingDate, e.closingDate, e.isExhibition, g.ikonid, g.name
        FROM exhibitions e
        JOIN galleries g ON g.ikonid=e.gallery_id
        WHERE title LIKE '%${this.search}%'
        ORDER BY e.openingDate DESC
        LIMIT ${this.limit} OFFSET ${this.page-1}*${this.limit};
        `;

        let results = this.$store.state.database.exec(sql);

        if(!results[0])
          return [];

        return results[0]['values'].map((row)=>{
          return {
            'id': row[0],
            'title': row[1],
            'openingDate': new Date(row[2]).toLocaleDateString(),
            'closingDate': isNaN(new Date(row[3])) ? "" : new Date(row[3]).toLocaleDateString(),
            'isExhibition': row[4],
            'gallery_id': row[5],
            'gallery': row[6]
          }
        });
      },

      count: function(){
        if(!this.$store.state.database)
          return NaN

        let sql = `
        SELECT COUNT(ikonid)
        FROM exhibitions
        `

        let results = this.$store.state.database.exec(sql);

        if(!results[0])
          return [];

        return results[0].values[0][0]
      },

      resultsCount: function(){
        if(!this.$store.state.database)
          return NaN

        let sql = `
        SELECT COUNT(ikonid)
        FROM exhibitions
        WHERE title LIKE '%${this.search}%'
        `

        let results = this.$store.state.database.exec(sql);

        if(!results[0])
          return [];

        return results[0].values[0][0]
      }
    },

    watch:{
      search: function(){
        this.page = 0;
      }
    }
  }
</script>